<template>
  <div class="template-container">
    <div class="template-item-wrap">
      <!-- 时间选择器 begin -->
      <div class="temp-item-inlineblock">
        <el-date-picker @change="pickerDateChange" v-model="pickerDate" type="daterange" align="left" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
        </el-date-picker>
      </div>
      <!-- 时间选择器 end  -->

      <div class="temp-item-inlineblock">
        <el-select v-model="reportParams.app_id" multiple filterable placeholder="请选择应用">
          <el-option v-for="item in appList" :key="item.app_id" :label="item.app_name" :value="item.app_id">
          </el-option>
        </el-select>
      </div>

      <div class="temp-item-inlineblock">
        <el-select v-model="reportParams.activity_id" size=90 multiple filterable placeholder="请选活动">
          <el-option v-for="item in activityList" :key="item.id" :label="item.name" :value="item.id">
          </el-option>
        </el-select>
      </div>

      <div class="temp-item-inlineblock">
        <el-button type="primary" v-on:click="searchGetAndroidChannelList">查询</el-button>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="exportExcel">导出为excel</el-button>
      </div>
    </div>
    <div class="table-wrap">
      <el-table height="700" ref="multipleTable" v-loading="loading" :data="channelReportList" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
        <el-table-column prop="date_of_log" label="日期" width="110" sortable>
        </el-table-column>
        <el-table-column prop="consumer_key" label="投放应用" width="220" sortable>
        </el-table-column>
        <el-table-column prop="activity_id" label="投放活动" sortable>
        </el-table-column>
        <el-table-column prop="clicks" label="点击数" width="140" sortable>
        </el-table-column>
        <el-table-column prop="effect_clicks" label="排重点击数" width="140" sortable>
        </el-table-column>
        <el-table-column prop="actives" label="激活数" width="140" sortable>
        </el-table-column>
        <el-table-column prop="registers" label="注册数" width="140" sortable>
        </el-table-column>
        <el-table-column prop="callbacks" label="回调数" width="140" sortable>
        </el-table-column>
        <el-table-column prop="active_rate" label="激活率(%)" width="140" sortable>
        </el-table-column>
      </el-table>
    </div>

  </div>

</template>

<script>
export default {
  created: function() {
    this.setPickerDate() // 设置默认查询日期
    this.getAppList()
    this.getActivityList()
    this.getChannelReport(this.reportParams)
  },
  data() {
    return {
      loading: false,
      reportParams: {
        sdate: '',
        edate: '',
        app_id: [],
        activity_id: []
      },
      appList: [],
      activityList: [],
      channelReportList: [],
      pickerDate: [
        new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 7),
        new Date()
      ],
      pickerOptions2: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去7天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去30天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      value6: '',
      value7: ''
    }
  },
  methods: {
    exportExcel() {
      var reportParams = ''
      for (var key in this.reportParams) {
        reportParams = reportParams + key + '=' + this.reportParams[key] + '&'
      }
      reportParams = reportParams.substring(0, reportParams.length - 1)
      window.location.href =
        '/Admin/Channelreport_Api/exportChannelReport?' + reportParams
    },
    setPickerDate() {
      const end_date = new Date()
      const start_date = new Date()
      start_date.setTime(start_date.getTime() - 3600 * 1000 * 24 * 7)
      this.reportParams.sdate =
        start_date.getFullYear() +
        '-' +
        (start_date.getMonth() + 1) +
        '-' +
        start_date.getDate()
      this.reportParams.edate =
        end_date.getFullYear() +
        '-' +
        (end_date.getMonth() + 1) +
        '-' +
        end_date.getDate()
    },
    pickerDateChange() {
      var start_date = new Date(this.pickerDate[0]),
        end_date = new Date(this.pickerDate[1])
      this.reportParams.sdate =
        start_date.getFullYear() +
        '-' +
        (start_date.getMonth() + 1) +
        '-' +
        start_date.getDate()
      this.reportParams.edate =
        end_date.getFullYear() +
        '-' +
        (end_date.getMonth() + 1) +
        '-' +
        end_date.getDate()
    },
    searchGetAndroidChannelList() {
      this.getChannelReport(this.reportParams)
    },
    getAppList() {
      var that = this
      this.$axios
        .get('/Admin/Channelreport_Api/getAppList')
        .then(function(response) {
          var respData = response.data
          if (respData.success) {
            that.appList = respData.data
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    getActivityList() {
      var that = this
      this.$axios
        .get('/Admin/Channelreport_Api/getActivityList')
        .then(function(response) {
          var respData = response.data
          if (respData.success) {
            // console.log(respData.data)
            that.activityList = respData.data
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    getChannelReport(params) {
      this.loading = true
      var that = this
      this.$axios
        .get('/Admin/Channelreport_Api/getChannelReport', {
          params: params
        })
        .then(function(response) {
          that.loading = false
          var respData = response.data
          if (respData.success) {
            var total, table
            table = respData.data.table
            if (table && table.length > 0) {
              total = respData.data.total
              total.date_of_log = '汇总'
              table.splice(0, 0, total)
              that.channelReportList = table
            } else {
              that.channelReportList = []
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.loading = false
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    }
  }
}
</script>


<style scoped>
.table-pagination {
  padding: 10px 0;
}
</style>



